.App {
  text-align: center;
}

th{
  font-family: Roboto;
font-style: normal;
font-weight: 500;
font-size: 12px;
line-height: 23px;
letter-spacing: 0.1em;

color: #000000;

}

.day{
  font-family: Roboto;
font-style: normal;
font-weight: normal;
font-size: 10px;
line-height: 23px;
letter-spacing: 0.1em;
color:#1d99d6;
}

.fold{
font-family: Roboto;
font-style: normal;
font-weight: normal;
font-size: 10px;
line-height: 21px;
color: white;
}

td{
  font-family: Roboto;
font-style: normal;
font-weight: 300;
font-size: 12px;
line-height: 23px;
letter-spacing: 0.1em;

color: #000000;
}

.folder{
position: absolute;
width: 210px;
height: 300px;
left: 90px;
top: 210px;
background: #1D99D6;
box-shadow: 0px 4px 27px rgba(0, 0, 0, 0.25);
border-radius: 36px 0px 0px 36px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.rectangle{
  width:1040px;
  height:520px;
  background-color: white;
  position: absolute;
  top:110px;
  left:300px;
  box-shadow: 0px 4px 27px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
}
body{
  background-color: #E5E5E5;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
